export default function calc(selCalc, postData) {
    let fileUpload = false;
    let calcData = {
        optionsPrice: 0,
        discount: 0
    };
    let priceData;
    
    fetch("http://localhost:3000/price").then((responce) => {
        if (responce.ok && responce.status == 200) {
            return responce.json();
        }
    }).then((data) => {
        priceData = data;
    });
    document.querySelector(selCalc).addEventListener('input', (event) => {
        if (priceData) {
            if (event.target.getAttribute('type') == "file") {
                fileUpload = true;
            }
            switch (event.target.id) {
                case "size":
                    if (event.target.value == "–í—ã–±–µ—Ä–∏—Ç–µ —Ä–∞–∑–º–µ—Ä –∫–∞—Ä—Ç–∏–Ω—ã") {
                        postData.size = 0;
                        calcData.sizePrice = 0;
                    } else {
                        postData.size = event.target.value;
                        calcData.sizePrice = priceData.priceSize[event.target.value];
                    }
                    break;
                case "material":
                    if (event.target.value == "–í—ã–±–µ—Ä–∏—Ç–µ –º–∞—Ç–µ—Ä–∏–∞–ª –∫–∞—Ä—Ç–∏–Ω—ã") {
                        postData.material = 0;
                        calcData.materialPrice = 0;
                    } else {
                        postData.material = event.target.value;
                        calcData.materialPrice = priceData.priceMaterial[event.target.value];
                    }
                    break;
                case "options":
                    if (event.target.value == "–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ —É—Å–ª—É–≥–∏") {
                        postData.options = "–ù–µ –≤—ã–±—Ä–∞–ª";
                        calcData.optionsPrice = 0;
                    } else {
                        postData.options = event.target.value;
                        calcData.optionsPrice = priceData.priceOptions[event.target.value];
                    }
                    break;
            }
            if (event.target.classList.contains("promocode")) {
                switch (event.target.value) {
                    case "IWANTPOPART":
                    calcData.discount = 0.7;
                    postData.discount = 0.7;
                        break;
                    default:
                    calcData.discount = 0;
                    postData.discount = "–ù–µ –≤–≤–µ–ª";
                        break;
                }
            }
            postToForms(calcData);
        } else {
            document.querySelector(".calc-price").textContent = "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫";
        }
    });
    
    function postToForms({sizePrice, materialPrice, optionsPrice, discount}) {
        if (sizePrice && materialPrice) {
            document.querySelector(".calc-price").textContent = `
                –°—É–º–º–∞ –∑–∞–∫–∞–∑–∞ ${Math.round(discount ? (sizePrice * materialPrice + optionsPrice) * discount : sizePrice * materialPrice + optionsPrice)}
            `;
            if (fileUpload) {
                document.querySelector(".calc .button-order").disabled = false;
                document.querySelector(".calc form").addEventListener('submit', () => {
                    calcData = {
                        sizePrice: 0,
                        materialPrice: 0,
                        optionsPrice: 0,
                        discount: 0
                    };
                    document.querySelector(".calc .button-order").disabled = true;
                    document.querySelector(".calc-price").textContent = "–î–ª—è —Ä–∞—Å—á–µ—Ç–∞ –Ω—É–∂–Ω–æ –≤—ã–±—Ä–∞—Ç—å —Ä–∞–∑–º–µ—Ä –∫–∞—Ä—Ç–∏–Ω—ã –∏ –º–∞—Ç–µ—Ä–∏–∞–ª –∫–∞—Ä—Ç–∏–Ω—ã";
                });
            }
        } else {
            document.querySelector(".calc .button-order").disabled = true;
            document.querySelector(".calc-price").textContent = "–î–ª—è —Ä–∞—Å—á–µ—Ç–∞ –Ω—É–∂–Ω–æ –≤—ã–±—Ä–∞—Ç—å —Ä–∞–∑–º–µ—Ä –∫–∞—Ä—Ç–∏–Ω—ã –∏ –º–∞—Ç–µ—Ä–∏–∞–ª –∫–∞—Ä—Ç–∏–Ω—ã";
        }
    }
}